.button {
  padding: 12px 24px;
  margin-top: 4rem;
  background:  #b3cde4; 
  color: #001b2e;
  font-size: 16px;
  font-weight: 600;
  border: none;
  border-radius: 8px; 
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
  
.button:hover {
  background: #85a0b8; 
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
}
  
.button:focus {
  outline: none;
  box-shadow: 0 0 0 4px rgba(59, 130, 246, 0.5);
}

.refresh-button {
  position: relative;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  background: transparent;
  border: 0px;
}

.hover-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.75);
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 14px;
  white-space: nowrap;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.image-button {
  transition: opacity 0.3s ease-in-out;
}
.image-button:hover .hover-text {
  opacity: 1; /* Show text when hovered */
}